*{
    margin:0;
    padding:0;
}
.user
{
    box-shadow:10px 8px 15px -2px rgba(135,135,135,0.37);
    background-color:rgb(224,79,190);
    margin-bottom:30px;
    width:600px;
    padding:20px;

}
.user h2,.user h3
{
    color:rgba(0,0,0,0.541);
}
.user span
{
    font-weight:500;
    color:black;
}
.user_delete
{
    border:none;
    outline:none;
    padding:10px;
    width:100px;
    background-color:brown;
    border-radius:8px;
    color:white;
    cursor:pointer;
}
.user_bottom
{
    margin-bottom:20px;
}